import React, { PureComponent } from "react";
import { Link, NavLink } from "react-router-dom";

import { to } from "../../../../platform/constants/routes";

import Languages from "../../../../platform/services/languages";
import { LanguageEnum } from "../../../../platform/constants/enums";

import "./style.scss";
import LeftBar from "./left-bar";
import Modal from "../../../modal";

const { language_label, home } = Languages.Translations;

class Header extends PureComponent {
  state = {
    isHidden: false,
    hasShadow: true,
    leftBar: false,
    selected: +window.localStorage.getItem("language") || LanguageEnum.en
  };

  changeLanguage = shortCode => {
    window.localStorage.setItem("language", shortCode);
    window.location.reload();
  };

  openLeftBar = () => {
    this.setState({
      leftBar: true
    });
  };

  closeLeftBar = () => {
    this.setState({
      leftBar: false
    });
  };

  render() {
    const { home } = Languages.Translations;

    const { isHidden, hasShadow, leftBar, selected } = this.state;

    if (
      window.location.pathname == "/auth/sign-in" ||
      window.location.pathname == "/auth/sign-up" ||
      window.location.pathname == "/auth/password-matches" ||
      window.location.pathname == "/auth/password-recovery"
    ) {
      this.setState({
        isHidden: true,
        hasShadow: false
      });
    } else {
      this.setState({
        isHidden: false,
        hasShadow: true
      });
    }

    return (
      <header>
        <div className="header-left-side G-flex G-justify-between G-align-center">
          <div className="sidebar-switcher" onClick={this.openLeftBar}>
            <span></span>
            <span></span>
            <span></span>
          </div>
          <div className="select-city G-flex G-justify-between G-align-center">
            <i className="icon-ic_location" />
            <span>Yerevan</span>
            <i className="icon-ic_arrowdown" />
          </div>
        </div>

        <div className="header-right-side G-flex G-justify-between G-align-center">
          <div className="select-language G-flex G-justify-between G-align-center">
            <span
              className={`${
                selected === LanguageEnum.hy ? "selected-language" : ""
              }`}
              onClick={() => this.changeLanguage(LanguageEnum.hy)}
            >
              հայ
            </span>
            <span
              className={`${
                selected === LanguageEnum.ru ? "selected-language" : ""
              }`}
              onClick={() => this.changeLanguage(LanguageEnum.ru)}
            >
              en
            </span>
            <span
              className={`${
                selected === LanguageEnum.en ? "selected-language" : ""
              }`}
              onClick={() => this.changeLanguage(LanguageEnum.en)}
            >
              рус
            </span>
          </div>
          <div className="phone-num">
            <a href="tel:(374) 010555555">(374) 010555555</a>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
